	import {
	  uniIcons,
	  uniNavBar,
    uniSearchBar 
	} from '@dcloudio/uni-ui'
	export default {
	  components: {
	    uniIcons,
	    uniNavBar,
      uniSearchBar 
	  },
		data() {
			return {
				searchTitle: null,
        current: 0, // tabs组件的current值，表示当前活动的tab选项
        swiperCurrent: 0, // swiper组件的current值，表示当前那个swiper-item是活动的
        tabList: [
          {
            name: '热搜'
          },
          {
            name: '影视'
          },
          {
            name: '小说'
          },
          {
            name: '科学'
          },
          {
            name: '体育'
          },
          {
            name: '会员推荐'
          }
        ],
        swiperList: [
          {
            list: ['热搜1', '热搜2', '热搜1', '热搜2', '热搜1', '热搜2', '热搜1', '热搜2', '热搜1', '热搜2']
          },
          {
            list: ['影视1', '影视2', '影视1', '影视2', '影视1', '影视2', '影视1', '影视2', '影视1', '影视2']
          },
          {
            list: ['小说1', '小说2', '小说1', '小说2', '小说1', '小说2', '小说1', '小说2', '小说1', '小说2']
          },
          {
            list: ['科学1', '科学2', '科学1', '科学2', '科学1', '科学2', '科学1', '科学2', '科学1', '科学2']
          },
          {
            list: ['体育1', '体育2', '体育1', '体育2', '体育1', '体育2', '体育1', '体育2', '体育1', '体育2']
          },
          {
            list: ['会员推荐1', '会员推荐2', '会员推荐1', '会员推荐2', '会员推荐1', '会员推荐2', '会员推荐1', '会员推荐2', '会员推荐1', '会员推荐2',]
          }
        ],
			}
		},
		methods: {
      backPrePage() {
        uni.navigateBack()
      },
			search() {
        console.log(this.searchTitle)
      },
      // tabs通知swiper切换
      tabsChange(index) {
        // console.log(index)
        this.swiperCurrent = index;
      },
      // swiper-item左右移动，通知tabs的滑块跟随移动
      transition(e) {
        let dx = e.detail.dx;
        this.$refs.uTabs.setDx(dx);
      },
      // 由于swiper的内部机制问题，快速切换swiper不会触发dx的连续变化，需要在结束时重置状态
      // swiper滑动结束，分别设置tabs和swiper的状态
      animationfinish(e) {
        let current = e.detail.current;
        this.$refs.uTabs.setFinishCurrent(current);
        this.swiperCurrent = current;
        this.current = current;
      },
		}
	}
